*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(0, 0, 0);
  color: rgb(3 0 177);
  color: linear-gradient(90deg, rgba(1,0,48,1) 0%, rgba(0,18,103,1) 100%, rgba(0,0,0,1) 100%, rgba(0,0,0,1) 100%);
  

}
.clock{
  display: flex;
  font-family: 'Audiowide', cursive;
  font-size: 60px;
  text-align: center;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-flow: column;
  padding-top: 18%
}
.first-half{
   display: inherit;
   flex-flow: row
}

.sec {
  width: 107px;
  animation: blinker 1s ease infinite
}
.footercss{
  display: flex;
  justify-content: center;
  padding: 6% 0;
  width: auto
}
.footerdiv{
  color: white;
  border: double rgba(255,255,255,.25);
  border-width: 4px 0;
}
h2{
  margin: 3% 0;
}

a{
  text-decoration: none; 
  color: white
}
a:hover{
  color: #00a96b;
}

@keyframes blinker {
  0%{
      opacity: 0;
  }
}